import * as firebase from 'firebase';
import flamelink from 'flamelink';
import { IPost } from '../interfaces';
const firebaseConfig = {
  apiKey: "AIzaSyDOzojA27MpgPL1mIqWI7w6kHCr4gXUeZg",
  authDomain: "theexuberantindian.firebaseapp.com",
  databaseURL: "https://theexuberantindian.firebaseio.com",
  projectId: "theexuberantindian",
  storageBucket: "theexuberantindian.appspot.com",
  messagingSenderId: "884173947980"
};
let firebaseApp = null;

if (!firebase.apps.length) {
  firebaseApp = firebase.initializeApp(firebaseConfig);

} else {
  firebaseApp = firebase;
}
const app = flamelink({ firebaseApp });

export const findAllPosts = async (): Promise<IPost[]> => {

  try {
    const data = await app.content.get('post')
    const blogposts: IPost[] = Object.values(data);
    if (!Array.isArray(blogposts)) {
      // throw new Error('Cannot find posts')4
      console.log('error');
    }
    return blogposts;
  } catch (error) {
    return error;
  }
}

export const getImage = (imageName: string): string => {
  return `https://firebasestorage.googleapis.com/v0/b/theexuberantindian.appspot.com/o/flamelink%2Fmedia%2F${imageName}?alt=media&token=8a3b4b12-52b8-463e-83ac-0c872a7d57a8`
}

export const findOnePost = async (slug: string): Promise<IPost> => {

  try {
    const post: IPost[] = await app.content.getByField('post', 'slug', slug);
    const blogPost: IPost = Object.values(post)[0];
    return blogPost;
  } catch (error) {
    return error;
  }
}